import React from 'react'
import PropTypes from 'prop-types'
import RadioQuestion from '../questions/RadioQuestion'
import TextField from '../questions/TextField'
import DropDownQuestion from '../questions/DropDownQuestion'
// import { Segment } from 'semantic-ui-react'
import { DateTimePicker } from '../questions/DatePicker'

const DesignInfo = ({ id, present }) => (
  <div>
    <TextField name='designerName' present={present} label='Designer Name(s):' placeholder='Susan Andersen' />
    <TextField name='structureID' present={present} label='Structure ID:' placeholder='COMPLEX_323123 ... 1233' />
    <DateTimePicker name='observationDate' present={present} title='Observation Date:' />
    <TextField name='designVideo' present={present} label='Design Video:' type='url' placeholder='https://youtu.be/SB-qEYVdvXA' />
    <DropDownQuestion
      name='designFlowCondition'
      present={present}
      title={'Design Flow Conditions'}
      fluid selection
      options={[
        { text: 'Baseflow', value: 'BASEFLOW' },
        { text: 'Spring runoff', value: 'SPRING_RUNOFF' },
        { text: 'Flood', value: 'FLOOD' },
        { text: 'Post Flood', value: 'POST_FLOOW' }
      ]}
    />
    <RadioQuestion
      name='designType'
      present={present}
      title={'Design Type'}
      options={[
        { label: 'Beaver Dam Analogue', value: 'BDA' },
        { label: 'Post Assisted Log Structure', value: 'PALS' },
        { label: 'Unanchored/Pinned Wood Addition', value: 'UNANCHORED_WOOD' }
      ]}
    />
  </div>
)
DesignInfo.propTypes = {
  id: PropTypes.number,
  present: PropTypes.bool
}

export default DesignInfo
