import { compose, withHandlers, lifecycle, mapProps } from 'recompose'
import { actions } from './reducers'
import { connect } from 'react-redux'

const mapStateToFieldProps = (state, props) => {
  const { editing, structures } = state
  const { name } = props
  let formGuid = ''
  let currField = { value: '', note: '' }
  try {
    currField = structures[editing.guid].fields[name] || {}
    formGuid = editing.guid
  }
  catch (e) { }
  return {
    formGuid,
    value: currField.value,
    note: currField.note
  }
}

export const withReduxField = compose(
  connect(mapStateToFieldProps), // Once connect() is composed `dispatch` is prop.
  // ONCE we mount the control we should initialize it
  lifecycle({
    componentDidMount () {
      const { dispatch, formGuid, section, name, present } = this.props
      if (!present) {
        console.log('componentDidMount', formGuid, section, name)
        dispatch(actions.initializeField(formGuid, name, section))
      }
    }
  }),
  withHandlers({
    setValue: ({ dispatch, formGuid, name }) => (newVal) => {
      console.log('setValue called', formGuid, name, newVal)
      dispatch(actions.changeFieldValue(formGuid, name, newVal))
    },
    setNote: ({ dispatch, formGuid, name }) => (newNote) => {
      console.log('setNote called', formGuid, name, newNote)
      dispatch(actions.changeFieldValue(formGuid, name, null, newNote))
    }
  }),
  // Make sure dispatch and formGuid never make it to the individual components
  mapProps(({ dispatch, formGuid, ...otherProps }) => otherProps)
)

const mapStateToHomeProps = ({ editing }, props) => ({
  editing
})

/**
 * HOMEPAGE state
 */

export const withReduxHome = compose(
  connect(mapStateToHomeProps), // Once connect() is composed `dispatch` is prop.
  withHandlers({
    backToHome: ({ dispatch }) => (guid) => {
      console.log('backToHome called', guid)
      dispatch(actions.backToHome(guid))
    },
    viewStructure: ({ dispatch }) => (guid) => {
      console.log('viewStructure called', guid)
      dispatch(actions.viewStructure(guid))
    }
  })
)
