import React from 'react'
import PropTypes from 'prop-types'
import { Step, Segment, Header, Divider, Responsive, Sidebar, Menu, Icon, Form, Dimmer, Button } from 'semantic-ui-react'
import { compose, withState, mapProps } from 'recompose'
import { withReduxHome } from '../../../redux/withRedux'
import './TheFormComplex.css'

const TheFormComplex = ({ activeStep, steps, stepHeaders, sidebarVisible, setSidebarVisible, setActiveStep }) => {
  return (
    <div style={{ height: '100%' }}>
      <TopbarEditing
        activeStep={activeStep}
        sidebarVisible={sidebarVisible}
        steps={steps}
        setSidebarVisible={setSidebarVisible}
        setActiveStep={setActiveStep} />
      <Sidebar.Pushable as='div'>
        <Sidebar
          as={Menu}
          animation='overlay'
          icon='labeled'
          inverted
          onHide={this.handleSidebarHide}
          vertical
          visible={sidebarVisible}
          width='wide'
        >
          <Step.Group attached='top' style={{ marginTop: '40px' }}
            vertical
            ordered size='mini'
            fluid>
            {stepHeaders}
          </Step.Group>
        </Sidebar>

        <Sidebar.Pusher style={{ marginTop: '40px' }}>
          <Dimmer.Dimmable as={Segment} attached blurring>
            <Dimmer active={sidebarVisible} onClick={() => setSidebarVisible(false)} />
            <Responsive maxWidth='800'>
              <p>{steps[activeStep] && steps[activeStep].description}</p>
              <Divider />
            </Responsive>
            <Form>
              {steps.map(({ Component }, idx) => (
                <div key={`stepComp${idx}`} style={{ display: (activeStep === idx) || (!activeStep && idx === 0) ? '' : 'none' }}>
                  <Component />
                </div>
              ))}
            </Form>
            <Menu icon style={{ borderRadius: 0 }}>
              <Menu.Menu position='right'>
                {
                  activeStep < 1 ? null : (
                    <Menu.Item onClick={() => {
                      setActiveStep(activeStep - 1)
                      setSidebarVisible(false)
                    }
                    }>
                      <Icon name='arrow circle left' />Previous Step
                    </Menu.Item>
                  )
                }
                {
                  activeStep >= steps.length - 1 ? null : (
                    <Menu.Item onClick={() => {
                      setActiveStep(activeStep + 1)
                      setSidebarVisible(false)
                    }
                    }>
                      Next Step<Icon name='arrow circle right' />
                    </Menu.Item>
                  )
                }
              </Menu.Menu>
            </Menu>
          </Dimmer.Dimmable>
        </Sidebar.Pusher>
      </Sidebar.Pushable>

    </div>
  )
}

TheFormComplex.propTypes = {
  activeStep: PropTypes.number,
  steps: PropTypes.array,
  sidebarVisible: PropTypes.bool,
  setSidebarVisible: PropTypes.func,
  setActiveStep: PropTypes.func,
  stepHeaders: PropTypes.arrayOf(PropTypes.node)
}

export default compose(
  withState('activeStep', 'setActiveStep', 0),
  withState('sidebarVisible', 'setSidebarVisible', false),
  mapProps(initProps => {
    const confirmStep = {
      title: 'Confirmation',
      description: 'Make sure everything is right',
      Component: () => (
        <div className='presentForm'>
          {initProps.steps.map(({ Component }, idx) => <Component present key={`presentComponent${idx}`} />)}
          <ConfirmMenu />
        </div>
      )
    }
    const newSteps = [...initProps.steps, confirmStep] //

    const stepHeaders = newSteps.map(({ title, description, complete }, idx) => (
      <Step link key={idx}
        active={idx === initProps.activeStep}
        completed={!!complete} onClick={() => {
          initProps.setActiveStep(idx)
          initProps.setSidebarVisible(false)
        }
        }>
        <Step.Content>
          <Step.Title>{title}</Step.Title>
          <Step.Description>{description}</Step.Description>
        </Step.Content>
      </Step>
    ))

    return {
      ...initProps,
      steps: [...newSteps],
      stepHeaders: [...stepHeaders] // confirmHeader
    }
  })
)(TheFormComplex)

const ConfirmMenu = withReduxHome(({ editing: { guid }, deleteStructure, backToHome }) => (
  <Menu icon style={{ borderRadius: 0 }}>
    <Menu.Menu position='right'>
      <Menu.Item>
        <Button content='Cancel' color='red' icon='ban' onClick={() => {
          // deleteStructure(guid)
          backToHome()
        }} />
      </Menu.Item>
      <Menu.Item>
        <Button content='Save' color='green' icon='save' onClick={backToHome} />
      </Menu.Item>
    </Menu.Menu>
  </Menu>
))

const TopbarEditing = withReduxHome(({ backToHome, activeStep, sidebarVisible, steps, setSidebarVisible, setActiveStep }) => (
  <Menu className='theFormComplexMenu' fixed='top' icon inverted style={{ borderRadius: 0 }}>
    <Menu.Item onClick={backToHome}>
      <Icon name='home' />
    </Menu.Item>
    <Menu.Item name='bars' onClick={() => setSidebarVisible(!sidebarVisible)}>
      <Icon name='bars' />
    </Menu.Item>
    <Menu.Item style={{ maxWidth: '48%' }}>
      <Header as='div' style={{ fontSize: '0.8rem' }} inverted>Step {activeStep + 1}: {steps[activeStep].title}</Header>
    </Menu.Item>
    <Menu.Menu position='right'>
      {
        activeStep < 1 ? null : (
          <Menu.Item onClick={() => {
            setActiveStep(activeStep - 1)
            setSidebarVisible(false)
          }
          }>
            <Icon name='arrow circle left' />
          </Menu.Item>
        )
      }
      {
        activeStep >= steps.length - 1 ? null : (
          <Menu.Item onClick={() => {
            setActiveStep(activeStep + 1)
            setSidebarVisible(false)
          }}>
            <Icon name='arrow circle right' />
          </Menu.Item>
        )
      }
    </Menu.Menu>
  </Menu>
)

)
