import _ from 'lodash'
import { combineReducers } from 'redux'
import uuidv1 from 'uuid/v1'
export const actionConsts = {
  INIT_FIELD: 'INIT_FIELD',
  CHANGE_FIELD_VALUE: 'CHANGE_FIELD_VALUE',
  CREATE_STRUCTURE: 'CREATE_STRUCTURE',
  EDIT_STRUCTURE: 'EDIT_STRUCTURE',
  VIEW_STRUCTURE: 'VIEW_STRUCTURE',
  BACK_TO_HOME: 'BACK_TO_HOME',
  DELETE_STRUCTURE: 'DELETE_STRUCTURE',
  UPLOAD_STRUCTURE: 'UPLOAD_FORM'
}

/**
 * Return a random chance of something happening happening
 * @param {*} prob (in percent: 0-100)
 */
const randomFail = (prob) => {
  const rand = Math.random()
  return rand > parseFloat(prob) / 100
}

const getRandomArbitrary = (min, max) => Math.random() * (max - min) + min

/**
 * Actions are actually just really simple objects
 */
export const actions = {
  initializeField: (guid, fieldName, fieldSection) => ({ type: actionConsts.INIT_FIELD, guid, fieldName, fieldSection }),
  changeFieldValue: (guid, fieldName, fieldValue, fieldNote) => ({
    type: actionConsts.CHANGE_FIELD_VALUE,
    guid,
    fieldName,
    fieldValue,
    fieldNote
  }),
  backToHome: () => ({ type: actionConsts.BACK_TO_HOME }),

  createStructure: () => {
    const guid = uuidv1()
    return (dispatch) => {
      dispatch({ type: actionConsts.CREATE_STRUCTURE, guid })
      dispatch({ type: actionConsts.EDIT_STRUCTURE, guid })
    }
  },
  deleteStructure: (guid) => ({ type: actionConsts.DELETE_STRUCTURE, guid }),
  editStructure: (guid) => ({ type: actionConsts.EDIT_STRUCTURE, guid }),
  viewStructure: (guid) => ({ type: actionConsts.VIEW_STRUCTURE, guid }),
  uploadStructure: (guid) => {
    const type = actionConsts.UPLOAD_STRUCTURE
    return (dispatch) => {
      dispatch({ type, guid, uploading: true, uploaded: false, uploadFailed: false })
      setTimeout(() => {
        // Randomly fail and ask for retry
        if (randomFail(20)) dispatch({ type, guid, uploading: false, uploaded: true, uploadFailed: false })
        else dispatch({ type, guid, uploading: false, uploaded: false, uploadFailed: true })
      }, getRandomArbitrary(1000, 4000)) // between 3 and 10 seconds simulates a bad network connection
    }
  }
}

// Template for new structure form object
const structureTemplate = {
  uploaded: false,
  uploading: false,
  uploadFailed: false,
  fields: {
    // fieldname: {
    //   value: null,
    //   section: null
    // }
  }
}

const defaultState = {
  // editing: '3b99e3e0-7598-11e8-90be-95472fb3ecbd',
  // structures: {
  //   '3b99e3e0-7598-11e8-90be-95472fb3ecbd': {
  //     uploaded: false,
  //     uploading: false,
  //     uploadFailed: false,
  //     fields: [
  //       {
  //         value: 'SOMETHING',
  //         section: 1
  //       }
  //     ]
  //   }
  // }
  editing: null,
  structures: {

  }
}

/**
 * Our reducers are super simple too
 * @param {*} state
 * @param {*} action
 */
const structures = (state = defaultState.structures, action) => {
  const { guid, uploading, uploaded, uploadFailed, fieldName, fieldValue, fieldSection, fieldNote } = action
  let newState
  switch (action.type) {
    case actionConsts.CREATE_STRUCTURE:
      // Add a new object and set what we're currently editing
      return {
        ...state,
        [guid]: structureTemplate
      }

    case actionConsts.INIT_FIELD:
      newState = _.cloneDeep(state)
      // console.log('HERE I AM', { action, state })
      const currField = newState[guid].fields[fieldName] || { value: null }

      newState[guid].fields[fieldName] = {
        value: currField.value,
        section: fieldSection,
        note: currField.note
      }
      return newState

    case actionConsts.CHANGE_FIELD_VALUE:
      const oldVal = state[guid].fields[fieldName]
      newState = _.cloneDeep(state)
      const newVal = fieldValue || oldVal.value
      const newNote = fieldNote || oldVal.note
      newState[guid].fields[fieldName] = {
        ...oldVal,
        value: newVal,
        note: newNote
      }
      return newState

    case actionConsts.DELETE_STRUCTURE:
      newState = _.cloneDeep(state)
      delete newState[guid]
      return newState

    case actionConsts.UPLOAD_STRUCTURE:
      newState = _.cloneDeep(state)
      newState[guid] = {
        ...newState[guid],
        uploading,
        uploaded,
        uploadFailed
      }
      return newState
    default:
      return state
  }
}

const editing = (state = defaultState.editing, action) => {
  switch (action.type) {
    case actionConsts.EDIT_STRUCTURE:
      return {
        guid: action.guid,
        edit: true
      }
    case actionConsts.VIEW_STRUCTURE:
      return {
        guid: action.guid,
        edit: false
      }
    case actionConsts.BACK_TO_HOME:
      return null
    default:
      return state
  }
}

export default combineReducers({
  structures,
  editing
})
