import React from 'react'
import PropTypes from 'prop-types'
import LocationPicker from '../questions/LocationPicker'
import TextField from '../questions/TextField'
import RadioQuestion from '../questions/RadioQuestion'
import { mapBoxToken } from '../../../config'

const PositionAttrib = ({ id, present }) => (
  <div>
    <LocationPicker present={present} name='location' mapboxApiAccessToken={mapBoxToken} />
    <RadioQuestion
      name='structureChannelLocation'
      present={present}
      title='Structure Location Relative to Channel(s)'
      options={[
        { label: 'On Main Channel', value: 'MAIN_CHANNEL' },
        { label: 'On Right Side Channel(s)', value: 'RIGHT_SIDE_CHANNEL' },
        { label: 'On Left Side Channel(s)', value: 'LEFT_SIDE_CHANNEL' },
        { label: 'On Right Floodplain', value: 'FLOODPLAIN_L' },
        { label: 'On Left Floodplain', value: 'FLOODPLAIN_R' }
      ]}
    />
    <TextField
      name='complex'
      present={present}
      label='Complex:'
      placeholder='COMPLEX_323123 ... 1233' />
    <RadioQuestion
      name='complexExtended'
      present={present}
      options={[
        { label: 'Part of new dam complex', value: 'NEW_DAM_COMPLEX' },
        { label: 'Expansion of existing dam complex', value: 'EXPANSION' },
        { label: 'NA - Isolated Dam', value: 'NA_ISOLATED_DAM' },
        { label: 'NA - Non-Dam', value: 'NA_NON_DAM' }
      ]}
    />
  </div>
)
PositionAttrib.propTypes = {
  id: PropTypes.number,
  present: PropTypes.bool
}

export default PositionAttrib
