import React from 'react'
import PropTypes from 'prop-types'
import moment from 'moment'
import Swipeable from 'react-swipeable'
import { withDeleteStructure } from '../../../../graphql/queries'
import { List, Sidebar, Menu, Icon, Segment, Header, Confirm, Label } from 'semantic-ui-react'
import { compose, withStateHandlers, withProps } from 'recompose'

const StructureListItem = ({
  structure: { id, lastedit, body }, color,
  sidebarVisible, openMenu, closeMenu, itemClick, localOnly,
  deleteConfirmVisible, deleteClick, deleteCancel, deleteConfirm
}) => (
  <Sidebar.Pushable as={List.Item}>
    <Sidebar
      animation='push'
      as={Menu}
      icon='labeled'
      inverted
      direction='right'
      color='red'
      width='thin'
      visible={sidebarVisible}
    >
      <Menu.Item onClick={deleteClick} >
        <Icon name='trash' /> Delete
      </Menu.Item>
    </Sidebar>
    <Sidebar.Pusher>
      <Swipeable onSwipedLeft={openMenu} onSwipedRight={closeMenu}>
        <Segment style={{ minHeight: 85 }} onClick={itemClick} basic >
          { localOnly === true ? <Label icon='refresh' as='a' color='yellow' corner /> : null}
          <Header inverted onClick={() => {}}>
            {getHeader(JSON.parse(body))}
          </Header>
          {getMeta(JSON.parse(body))}
          {console.log(localOnly)}
        </Segment>
      </Swipeable>
    </Sidebar.Pusher>
    <Confirm
      header='Delete Structure?'
      open={deleteConfirmVisible}
      onCancel={deleteCancel}
      onConfirm={deleteConfirm}
      size='large'
    />
  </Sidebar.Pushable>
)

StructureListItem.propTypes = {
  structure: PropTypes.object,
  color: PropTypes.string,
  localOnly: PropTypes.bool,
  // recompose
  sidebarVisible: PropTypes.bool,
  deleteConfirmVisible: PropTypes.bool,
  openMenu: PropTypes.func,
  itemClick: PropTypes.func,
  closeMenu: PropTypes.func,
  deleteClick: PropTypes.func,
  deleteCancel: PropTypes.func,
  deleteConfirm: PropTypes.func
}
export default compose(
  withProps(({ structure: { id } }) => {
    const localOnly = id.indexOf('pending') === 0
    let color = null
    if (localOnly === true) color = 'yellow'
    // else if (!uploading && !uploaded && uploadFailed) color = 'red'
    return { color, localOnly }
  }),
  withDeleteStructure,
  withStateHandlers(
    {
      sidebarVisible: false,
      deleteConfirmVisible: false
    },
    {
      itemClick: ({ sidebarVisible }) => (e) => {
        console.log('click', e, sidebarVisible)
        if (sidebarVisible === true) return { sidebarVisible: false }
        else {
          console.log('NAVIGATE')
        }
      },
      openMenu: () => () => ({ sidebarVisible: true }),
      closeMenu: () => (e) => {
        e.stopPropagation() // Make sure we don't accidentally trigger  click
        return { sidebarVisible: false }
      },
      deleteClick: () => () => ({ deleteConfirmVisible: true }),
      deleteCancel: () => () => ({ deleteConfirmVisible: false }),
      deleteConfirm: (state, { deleteStructure, structure }) => () => {
        deleteStructure({ id: structure.id })
        return { deleteConfirmVisible: false, sidebarVisible: false }
      }
    }
  )
)(StructureListItem)

