/**
 * Heler function to make the header nice
 */
function getHeader (fields) {
  try {
    return fields.structureID.value
  }
  catch (e) {
    return <span style={{ color: 'red' }} >NO NAME</span>
  }
}

/**
 * Helper function to style the meta fields and date and stuff
 */
function getMeta (fields) {
  let observationDate
  let designerName
  try {
    observationDate = moment(new Date(fields.observationDate.value))
  }
  catch (e) { }
  try {
    designerName = fields.designerName.value
  }
  catch (e) { }

  return <div>
    <div>{designerName ? <span>Designed by <strong>{designerName}</strong> </span> : null}</div>
    <div style={{ fontSize: '0.9rem', color: '#888' }}>{observationDate ? <span> on <strong>{observationDate.format('dddd, MMMM Do') }</strong> </span> : null}</div>
  </div>
}
